html {
  height: 100%;
  font-family: 'Lato';
}

body {
  min-height: 100%;
  margin: 0;
  padding: 0;
  background-image: -webkit-linear-gradient(269deg, #47B1D2 0%, #216D85 100%);
  background-image: linear-gradient(-179deg, #47B1D2 0%, #216D85 100%);
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
}

.logo {
  display: block;
  width: 50%;
  margin: 0 auto;
  padding: 15% 0 0 0;
}

@media only screen and (orientation: landscape) {
  .logo {
    width: 250px;
    padding: 5% 0 0 0;
  }
}

.welcome {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 90%;
  margin: 20% auto 0 auto;
  background: #FFFFFF;
  border-radius: 5px;
}

.text-title {
  font-size: 18px;
  font-weight: bold;
  margin: 10% 0;
}

.text-restriction {
  text-align: center;
  margin: 0 0 10% 0;
}

@media only screen and (orientation: landscape) {
  .welcome  {
    width: 500px;
    margin: 5% auto 0 auto;
  }
}

.button-primary {
  display: block;
  background: #CC3E50;
  border-radius: 5px;
  width: 150px;
  height: 40px;
  margin: 5% auto;
  font-family: Lato;
  font-size: 18px;
  color: #FFFFFF;
  text-align: center;
  cursor: pointer;
}

.form-capture-age {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}

.request-age {
  font-size: 18px;
}

.input-age {
  width: 20%;
  font-size: 18px;
  text-align: center;
  background: #D8D8D8;
  border: 2px solid red;
}

.flex-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  width: 90%;
  margin: 0 auto 5% auto;
}

.beer {
  display: flex;
  justify-content: space-around;
  align-items: center;
  background: #EDEDED;
  border-radius: 5px;
  width: 47.5%;
  margin: 5% 0 0 0;
  cursor: pointer;
}

.beer-name {
  margin: 0;
  padding: 0;
  width: 50%;
}

.beer-image {
  margin: 5% 0;
  width: 20%;
}

@media only screen and (orientation: landscape) {

  .flex-container {
    justify-content: center;
  }

  .beer {
    width: 15%;
    margin: 2%;
  }
}

.details {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(56, 56, 56, 0.93);
}

.content {
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-wrap: nowrap;
  position: relative;
  width: 95%;
  height: 95%;
  background-image: -webkit-linear-gradient(269deg, #47B1D2 0%, #216D85 100%);
  background-image: linear-gradient(-179deg, #47B1D2 0%, #216D85 100%);
  border-radius: 5px;
}

.button-exit {
  position: absolute;
  top: 1%;
  right: 2%;
  cursor: pointer;
}

.text {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 50%;
  color: #EDEDED;
}

.name {
  font-size: 24px;
  padding: 0;
}

.tagline {
  font-size: 16px;
  padding: 5% 0 5% 0;
}

.description {
  font-size: 14px;
  padding: 5% 0;
}

.food {
  font-size: 14px;
  padding: 5% 0 0 0;
}

.beer-image-large {
  height: 60%;
}

hr {
  width: 100%;
}

@media only screen and (orientation: landscape) {
  .content {
    width: 600px;
    height: 600px;
    justify-content: center;
  }

  .text {
    width: 60%;
    -webkit-transform: translateX(-20%);
            transform: translateX(-20%);
  }

  .beer-image-large {
    position: absolute;
    height: 80%;
    top: 50%;
    right: 0;
    -webkit-transform: translateX(+50%) translateY(-45%);
            transform: translateX(+50%) translateY(-45%);
  }
}

